import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from "axios";

axios.defaults.baseURL="http://127.0.0.1:8000/api/";
axios.defaults.headers.common['Authorization']="Bearer " + localStorage.getItem("token");

///// dipakai untuk menjalankan bootstrap/////////
// # bootstrap tidak perlu di use pada APP
import "bootstrap/dist/css/bootstrap.css"
import "bootstrap/dist/js/bootstrap.js"
import "bootstrap-icons/bootstrap-icons.svg"
import 'bootstrap-icons/font/bootstrap-icons.css'
import "font-awesome/css/font-awesome.css"

import HighchartsVue from 'highcharts-vue'
import Highcharts from 'highcharts'
import exportingInit from 'highcharts/modules/exporting'
import exportingInit01 from 'highcharts/modules/export-data'
import exportingInit02 from 'highcharts/highcharts-more'
import exportingInit03 from 'highcharts/highcharts-3d'
import solidgauge from 'highcharts/modules/solid-gauge'

exportingInit(Highcharts)
exportingInit01(Highcharts)
exportingInit02(Highcharts)
exportingInit03(Highcharts)
solidgauge(Highcharts)


import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
// import 'jszip';
// import 'pdfmake';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import 'datatables.net-buttons/js/buttons.colVis';
import 'datatables.net-buttons/js/buttons.flash';
import 'datatables.net-buttons/js/buttons.html5';
import 'datatables.net-buttons/js/buttons.print';
////////////////////////////////////////////

createApp(App)
.use(HighchartsVue)
.use(store).use(router).mount('#app')



