<template>
  <div class="wrapper" style="padding-bottom: 200px">
    <content>
      <div class="login-box">
        <div class="login-logo">
          <a href="/">
            <b>{{ $store.state.app.name }}</b>
          </a>
        </div>

        <div class="card">
          <div class="card-body login-card-body">
            <p class="login-box-msg">Change Password</p>
            <form action="/login" method="post" @submit.prevent="edituser()">
              <div class="input-group mb-3">
                <input type="text" class="form-control" v-model="first_name" placeholder="first_name" disabled />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-user"></span>
                  </div>
                </div>
              </div>

              <div v-if="roleslevel >= 0" class="input-group mb-3">
                <select v-model="roleslevel" class="form-control" disabled>
                  <option disabled value="">Roles</option>
                  <option value="1">Operator</option>
                  <option value="51">Admin</option>
                  <option value="100">Enginnering</option>
                </select>
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fa fa-tasks"></span>
                  </div>
                </div>
              </div>

              <div class="input-group mb-3">
                <input type="password" class="form-control" v-model="password" placeholder="Password" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-lock"></span>
                  </div>
                </div>
              </div>
              <div class="input-group mb-3">
                <input type="password" class="form-control" v-model="password_confirm" placeholder="Confirm Password" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-lock"></span>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-8">
                  <div class="icheck-primary">
                    <!-- <input type="checkbox" id="remember" class="mr-2" />
                <label for="remember"> Remember Me </label> -->
                  </div>
                </div>

                <div class="col-4">
                  <button type="submit" class="btn btn-primary btn-block">
                    Change
                  </button>
                </div>
              </div>
            </form>

            <p class="mb-0 text-danger">
              {{ msg }}
            </p>
          </div>
        </div>
      </div>
    </content>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginEditUserView",
  data() {
    return {
      id: "7",
      // first_name: "d",
      // last_name: "d",
      // roles: "4",
      // email: "d@rucika.com",
      password: "",
      password_confirm: "",
      msg: "",
      rolesname: "",
      roleslevel: ""
    };
  },
  components: {},

  beforeMount() {
    $("body").removeClass("sidebar-mini").addClass("login-page");

    this.id = (localStorage.getItem("id"));
    this.first_name = (localStorage.getItem("first_name"));
    this.rolesname = (localStorage.getItem("roles") % 10);
    this.roleslevel = (localStorage.getItem("roles"))
    console.log(this.roleslevel);
  },
  methods: {

    async edituser() {

      const response = await axios
        .post("edit/17", {
          id: this.id,
          first_name: this.first_name,
          last_name: this.password,
          roles: parseInt(this.roleslevel),
          email: this.first_name + "@rucika.com",
          password: this.password,
          password_confirm: this.password_confirm,

        })
        .then((response) => {
          console.log(this.password);
          console.log(response);

          this.msg = "Password is changed";
          window.location.href = "/loginview";
        })
        .catch((error) => {
          // this.msg = error.message;
          if (error.response) {

            if (error.response.data.errors.email != null) { this.msg = "user already exist" }
            if (error.response.data.errors.password_confirm != null) { this.msg = error.response.data.errors.password_confirm; }
            if (error.response.data.errors.password != null) { this.msg = error.response.data.errors.password; }
            if (error.response.data.errors.roles != null) { this.msg = error.response.data.errors.roles; }
            if (error.response.data.errors.last_name != null) { this.msg = error.response.data.errors.last_name; }
            if (error.response.data.errors.first_name != null) { this.msg = error.response.data.errors.first_name; }
            //console.log(error.response.data.errors); // => the response payload
          }
        });
    },
  },
};
</script>
