<template>
  <div class="wrapper" style="padding-bottom: 200px">
    <content>
      <div class="login-box">
        <div class="login-logo">
          <a href="/">
            <b>{{ $store.state.app.name }}</b>
          </a>
        </div>

        <div class="card">
          <div class="card-body login-card-body">
            <p class="login-box-msg">Add New User</p>
            <form action="/login" method="post" @submit.prevent="adduser()">
              <div class="input-group mb-3">
                <input type="text" class="form-control" autocomplete="nope" @blur="handleType" @focus="handleType"
                  v-model="first_name" placeholder="first_name" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-user"></span>
                  </div>
                </div>
              </div>
              <div class="input-group mb-3">
                <input type="text" class="form-control" autocomplete="off" @blur="handleType" @focus="handleType"
                  v-model="op_no" placeholder="operator no" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-user"></span>
                  </div>
                </div>
              </div>
              <div class="input-group mb-3">
                <input type="text" class="form-control" autocomplete="off" @blur="handleType" @focus="handleType"
                  v-model="op_stamp" placeholder="operator stamp" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-user"></span>
                  </div>
                </div>
              </div>
              <div class="input-group mb-3">
                <select v-model="line" class="form-control">
                  <option disabled value="">Select Line</option>
                  <option value="Line 1">Line 1</option>
                  <option value="Line 2">Line 2</option>
                  <option value="Line 3">Line 3</option>
                  <option value="Line 4">Line 4</option>
                  <option value="Engineering">Engineering</option>
                  <option value="IT">IT</option>
                </select>
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fa fa-tasks"></span>
                  </div>
                </div>
              </div>
              <div v-if="roleslevel < 100" class="input-group mb-3">
                <select v-model="roles" class="form-control">
                  <option disabled value="">Select Roles</option>
                  <option value="1">Operator</option>
                  <option value="50">Admin</option>
                </select>
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fa fa-tasks"></span>
                  </div>
                </div>
              </div>

              <div v-if="roleslevel >= 100" class="input-group mb-3">
                <select v-model="roles" class="form-control">
                  <option disabled value="">Select Roles</option>
                  <option value="1">Operator</option>
                  <option value="51">Admin</option>
                  <option value="100">Enginnering</option>
                </select>
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fa fa-tasks"></span>
                  </div>
                </div>
              </div>

              <div class="input-group mb-3">
                <input type="password" class="form-control" v-model="password" placeholder="Password" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-lock"></span>
                  </div>
                </div>
              </div>
              <div class="input-group mb-3">
                <input type="password" class="form-control" v-model="password_confirm" placeholder="Confirm Password" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-lock"></span>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-12">
                  <div class="icheck-primary">
                    <!-- <input type="checkbox" id="remember" class="mr-2" />
                <label for="remember"> Remember Me </label> -->
                  </div>
                </div>
                <div class="col-6">

                </div>
                <div class="col-6">
                  <button type="submit" class="btn btn-primary btn-block">
                    <span class="fas fa-plus"></span>
                    <span class="fas fa-address-card" style="margin-left: 5px;"></span>
                    Add User
                  </button>
                </div>
              </div>
            </form>

            <p class="mb-0 text-danger">
              {{ msg }}
            </p>
          </div>
        </div>
      </div>
    </content>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginAddUserView",
  data() {
    return {
      first_name: "",
      last_name: "",
      roles: "",
      op_no: "",
      op_stamp: "",
      line: "",
      email: "",
      password: "",
      password_confirm: "",
      msg: "",
      rolesname: "",
      roleslevel: ""
    };
  },
  components: {},

  beforeMount() {
    $("body").removeClass("sidebar-mini").addClass("login-page");

    this.rolesname = (localStorage.getItem("roles") % 10);
    this.roleslevel = (localStorage.getItem("roles"))
    console.log(this.rolesname);
  },
  methods: {

    async adduser() {

      const response = await axios
        .post("register", {
          first_name: this.first_name,
          last_name: this.password,
          op_no: this.op_no,
          op_stamp: this.op_stamp,
          line: this.line,
          roles: parseInt(this.roles),
          email: this.first_name + "@rucika.com",
          password: this.password,
          password_confirm: this.password_confirm,
        })
        .then((response) => {
          console.log(this.password);
          this.msg = "user created";
          window.location.href = "/loginview";
        })
        .catch((error) => {
          // this.msg = error.message;
          if (error.response) {

            if (error.response.data.errors.email != null) { this.msg = "user already exist" }
            if (error.response.data.errors.password_confirm != null) { this.msg = error.response.data.errors.password_confirm; }
            if (error.response.data.errors.password != null) { this.msg = error.response.data.errors.password; }
            if (error.response.data.errors.roles != null) { this.msg = error.response.data.errors.roles; }
            if (error.response.data.errors.last_name != null) { this.msg = error.response.data.errors.last_name; }
            if (error.response.data.errors.first_name != null) { this.msg = error.response.data.errors.first_name; }
            if (error.response.data.errors.op_name != null) { this.msg = error.response.data.errors.op_name; }
            if (error.response.data.errors.op_stamp != null) { this.msg = error.response.data.errors.op_stamp; }
            if (error.response.data.errors.line != null) { this.msg = error.response.data.errors.line; }



            //console.log(error.response.data.errors); // => the response payload
          }
        });
    },
  },
};
</script>
