<template>
  <div class="wrapper" style="padding-bottom: 200px">
    <content>
      <div class="login-box">
        <div class="login-logo">
          <a href="/">
            <b>{{ $store.state.app.name }}</b>
          </a>
        </div>

        <div class="card">
          <div class="card-body login-card-body">
            <p class="login-box-msg">Sign in to start your session</p>
            <form action="/login" method="post" @submit.prevent="loginSubmit()">
              <div class="input-group mb-3">
                <input type="text" class="form-control" v-model="first_name" placeholder="first_name" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-user"></span>
                  </div>
                </div>
              </div>
              <div class="input-group mb-3">
                <input type="password" class="form-control" v-model="password" placeholder="Password" />
                <div class="input-group-append">
                  <div class="input-group-text">
                    <span class="fas fa-lock"></span>
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-8">
                  <div class="icheck-primary">
                    <!-- <input type="checkbox" id="remember" class="mr-2" />
                <label for="remember"> Remember Me </label> -->
                  </div>
                </div>

                <div class="col-4">
                  <button type="submit" class="btn btn-primary btn-block">
                    Sign In
                  </button>
                </div>
              </div>
            </form>

            <p class="mb-0 text-danger">
              {{ msg }}
            </p>
          </div>
        </div>
      </div>
    </content>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginView",
  data() {
    return {
      first_name: "",
      password: "",
      msg: "",
    };
  },
  components: {},

  beforeMount() {
    $("body").removeClass("sidebar-mini").addClass("login-page");
  },
  methods: {
    async loginSubmit() {
      const response = await axios
        .post("login", {
          first_name: this.first_name,
          password: this.password,
        })
        .then((response) => {
          // console.log(response);
          localStorage.setItem("token", response.data.token);
          localStorage.setItem("roles", response.data.user.roles);
          localStorage.setItem("id", response.data.user.id);
          localStorage.setItem("first_name", response.data.user.first_name);
          localStorage.setItem("line", response.data.user.line);
          localStorage.setItem("op_no", response.data.user.op_no);
          localStorage.setItem("op_stamp", response.data.user.op_stamp);



          this.$store.dispatch("user", response.data.user);
          // console.log(localStorage);

          window.location.href = "/";
        })
        .catch((error) => {
          this.msg = error.message;
          if (error.response) {
            this.msg = error.response.data.message;
            console.log(error.response.data); // => the response payload
          }
        });
    },
  },
};
</script>
