import { createStore } from 'vuex'

export default createStore({
  state: {
    app:{
      name:'Pump Station'
    },
    items: [],
    // icon: '',
    
  },
  getters: {
    user:(state)=>{
      return state.user;
    },
  },
  mutations: {
    user (state,user){
      state.user = user
    },
    addCustomer (state, item) {
      // mutate state
      state.items.push(item);
      // state.icons = icon;
    },
  },
  actions: {
    user(context,user){
      context.commit('user',user);
    },
  },
  modules: {
  }
})
