<script>
import { mapGetters } from "vuex";

export default {
  name: "Navbar",

  props: {
    items: Array,
  },
  data() {
    return {
      userlevels: null,
      userroles: null,
      BDate:
        new Date().getFullYear() +
        "-" +
        (new Date().getMonth() + 1).toString().padStart(2, "0") +
        "-01",
      SDate: new Date().toLocaleDateString("fr-CA"),
      strpage: "Home",
    };
  },

  methods: {
    Logout() {
      localStorage.removeItem("token");
      localStorage.removeItem("roles");
      localStorage.removeItem("first_name");
      this.$store.dispatch("user", null);
    },


    setdate() {
      var m1 = (new Date(new Date()).getMonth() + 1)
        .toString()
        .padStart(2, "0");
      var y1 = new Date().getFullYear();
      var d1 = y1 + "-" + m1 + "-" + "01";
      this.BDate = d1;
      var date01 = new Date(new Date()).getDate();
      var d2 = y1 + "-" + m1 + "-" + date01;
      //console.log(new Date(d2))

      this.$store.dispatch("jamnow", d2);
      this.SDate = new Date(this.$store.state.jamnow).toLocaleDateString(
        "fr-CA"
      );
      //console.log(this.SDate)
    },

  },
  computed: {
    ...mapGetters(["user"]),
  },

  mounted() {
    if (localStorage.getItem("roles") >= 50) {
      this.userroles = "admin";
      this.userlevels = localStorage.getItem("roles");
    } else {
      this.userroles = "operator";
      this.userlevels = localStorage.getItem("roles");
    }
  },

