    <!-- <h1 class="gun">test</h1> -->
    <!-- <button @click="changemsg">ganti message</button>
    <button @click="changemsg02">ganti coordinate</button>
    <button @click="loop01">Looping</button> -->
    <Map :mapscene='mapscene' :well='well' :mark='mark' :cntflg='cntflg' />
  </div>
</template>
<script>
import { ref, reactive } from "vue";
import Map from '@/components/Map/Map.vue';

export default {
  name: 'PumpStationView',
  components: {
    Map
  },

  setup() {
    const mapscene = ref("PT EMP")
    const cntflg = ref(0)
    const well = reactive(
      [{
        place: 'pekanbaru',
        long: '101.4478',
        lat: '-0.5071',
        mode: 'card',
        group: '01',
      }, {
        place: 'medan',
        long: '98.6722',
        lat: '3.5952',
        mode: 'icon',
        group: '01',
      }
        , {
        place: 'jakarta',
        long: '106.8229',
        lat: '-6.1944',
        mode: 'widget',
        group: '02',
      }, {
        place: 'bandung',
        long: '107.6191',
        lat: '-6.9175',
        mode: 'carousel',
        group: '02',
      }, {
        place: 'banjarmasin',
        long: '114.5944',
        lat: '-3.3186',
        mode: 'table',
        group: '03',
      }
        , {
        place: 'balikpapan',
        long: '116.8529',
        lat: '-1.2379',
        mode: 'cardicon',
        group: '03',
      }]
    )
    const mark = reactive(
      [{
        marker: '',
        popup: ''
      }, {
        marker: '',
        popup: ''
      }, {
        marker: '',
        popup: ''
      }, {
        marker: '',
        popup: ''
      }, {
        marker: '',
        popup: ''
      }, {
        marker: '',
        popup: ''
      }]
    )

    const changemsg = () => {
      mapscene.value = "test";
      cntflg.value++;
      well[0].lang = '102.4478',
        well[0].lat = '-0.5071',
        well[0].text = "pekan baru saja"
    }

    const changemsg02 = () => {
      mapscene.value = "test";
      cntflg.value++;
      well[0].lang = '107.6191',
        well[0].lat = '-6.9175',
        well[0].text = "pindah bandung"
    }

    const loop01 = () => {
      setInterval(() => {

        var simvalue = ((new Date()).getSeconds()).toString();
        mapscene.value = simvalue;
        cntflg.value++;
        well[0].lang = '102.4478';
        well[0].lat = '-0.5071';
        well[0].text = simvalue;
        // console.log(well[0].text)

      }, 1000);
    }

    return {
      mapscene, cntflg, well, mark, changemsg, changemsg02, loop01
    }
  },


}
</script>

